@font-face {
	font-family: 'Cambria';
	src: local('Cambria Italic'), local('Cambria-Italic'),
    url('./Cambria-Italic.woff2') format('woff2'),
    url('./Cambria-Italic.woff') format('woff'),
		url('./Cambria-Italic.eot?#iefix') format('embedded-opentype'),
		url('./Cambria-Italic.ttf') format('truetype');
	font-weight: 400;
	font-style: italic;
  font-display: swap;
}

@font-face {
	font-family: 'Cambria';
	src: local('Cambria Bold'), local('Cambria-Bold'),
    url('./Cambria-Bold.woff2') format('woff2'),
    url('./Cambria-Bold.woff') format('woff'),
		url('./Cambria-Bold.eot?#iefix') format('embedded-opentype'),
		url('./Cambria-Bold.ttf') format('truetype');
	font-weight: 700;
	font-style: normal;
  font-display: swap;
}

